<template>
    <div>
        <div class="nav-head">
            <div class="nav-title">
                {{ title == "书架" ? "我的" : "" }}{{ title }}
            </div>
            <van-icon
                class="search-icon"
                name="search"
                @click="routeToSearch"
            />
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "NavHead",
    computed: {
        ...mapState({
            title: (state) => {
                return state.navActive;
            },
        }),
    },
    methods: {
        routeToSearch() {
            this.$router.push({ name: "Search" });
        },
    },
};
</script>

<style lang="less" scoped>
.nav-head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    background-color: #e4393c;
    color: #fff;
    .nav-title {
        font-size: 16px;
        font-weight: bold;
    }

    .search-icon {
        font-size: 18px;
    }
}
</style>
