<template>
    <div>
        <NavHead></NavHead>
        <div class="rank">
            <div class="rank-main-title">
                <ul class="main-title">
                    <li
                        class="main-title_item"
                        v-for="(item, index) in maintitle"
                        :key="index"
                        :class="{ active: item.active }"
                        @click="changeMainActive(index)"
                    >
                        <span>{{ item.text }}</span>
                    </li>
                </ul>
            </div>
            <div class="rank-subtitle">
                <ul class="subtitle">
                    <li
                        class="subtitle_item"
                        v-for="(item, index) in currentSubtitle"
                        :key="index"
                        :class="{ active: item.active }"
                        @click="changeSubtitle(index)"
                    >
                        <span>{{ item.shortTitle }}</span>
                    </li>
                </ul>
                <ul class="books">
                    <BookCard
                        v-for="(item, index) in currentBooks"
                        :key="index"
                        :item="item"
                    ></BookCard>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import NavHead from "@/components/NavHead.vue";
import { mapState, mapActions } from "vuex";
import types from "@/store/types.js";
import { getData } from "@/api/api.js";
import store from "@/store/index.js";
import BookCard from "@/components/BookCard.vue";
export default {
    name: "BookRank",
    components: {
        NavHead,
        BookCard,
    },
    data() {
        return {
            maintitle: [],
            currentSubtitle: [],
        };
    },
    computed: {
        ...mapState({
            currentBooks: (state) => state.currentRankBooks,
        }),
    },
    methods: {
        ...mapActions([types.GET_RANK_BOOKS]),
        changeMainActive(i) {
            this.maintitle.forEach((el, index) => {
                el.active = false;
                if (i == index) {
                    el.active = true;
                    this.currentSubtitle = el.subtitle;
                    this.currentSubtitle.forEach((ele) => {
                        if (ele.active) {
                            this[types.GET_RANK_BOOKS]({
                                id: ele._id,
                            });
                        }
                    });
                }
            });
        },
        changeSubtitle(i) {
            this.currentSubtitle.forEach((el, index) => {
                el.active = false;

                if (i == index) {
                    el.active = true;
                    this[types.GET_RANK_BOOKS]({ id: el._id });
                }
            });
        },
    },
    watch: {
        currentSubtitle(newVal, oldVal) {
            oldVal.forEach((el, index) => {
                if (index == 0) {
                    el.active = true;
                } else {
                    el.active = false;
                }
            });
        },
    },
    beforeRouteEnter(to, from, next) {
        let rankSubtitle = JSON.parse(localStorage.getItem("rankSubtitle"));
        let maintitle = [
            {
                text: "男频",
                target: "male",
                active: true,
                subtitle: [],
            },
            {
                text: "女频",
                target: "female",
                active: false,
                subtitle: [],
            },
            {
                text: "出版",
                target: "epub",
                active: false,
                subtitle: [],
            },
        ];
        if (rankSubtitle && rankSubtitle.expires > Date.now()) {
            maintitle.forEach((el) => {
                let currentSubtitleArr = rankSubtitle.data[el.target];
                currentSubtitleArr.forEach((ele, index) => {
                    if (index == 0) {
                        ele.active = true;
                    } else {
                        ele.active = false;
                    }
                });
                el.subtitle = currentSubtitleArr;
            });
            store.dispatch(types.GET_RANK_BOOKS, {
                id: maintitle[0].subtitle[0]._id,
            });
            next((vm) => {
                vm.maintitle = maintitle;
                vm.currentSubtitle = maintitle[0].subtitle;
            });
        } else {
            store.state.loading = true;
            getData("https://novel.kele8.cn/rank-category")
                .then((res) => {
                    localStorage.setItem(
                        "rankSubtitle",
                        JSON.stringify({
                            expires: Date.now() + 3 * 24 * 60 * 60 * 1000,
                            data: res.data,
                        })
                    );
                    maintitle.forEach((el) => {
                        let currentSubtitleArr = res.data[el.target];
                        currentSubtitleArr.forEach((ele, index) => {
                            if (index == 0) {
                                ele.active = true;
                            } else {
                                ele.active = false;
                            }
                        });
                        el.subtitle = currentSubtitleArr;
                    });
                    store.dispatch(types.GET_RANK_BOOKS, {
                        id: maintitle[0].subtitle[0]._id,
                    });
                    store.state.loading = false;
                    next((vm) => {
                        vm.maintitle = maintitle;
                        vm.currentSubtitle = maintitle[0].subtitle;
                    });
                })
                .catch((err) => {
                    console.log(err);
                    store.state.loading = false;
                });
        }
    },
};
</script>

<style lang="less" scoped>
.rank {
    .rank-main-title {
        .main-title {
            padding: 5px;
            display: flex;
            .main-title_item {
                flex: 4;
                padding: 5px;
                span {
                    padding-bottom: 10px;
                    border-bottom: 2px solid transparent;
                }
                &.active span {
                    border-bottom-color: #e4393c;
                    color: #e4393c;
                }
            }
        }
    }
    .rank-subtitle {
        display: flex;
        margin-top: 2px;
        .subtitle {
            flex: 2;
            max-height: 79vh;
            overflow-y: scroll;
            .subtitle_item {
                padding: 15px 0;
                font-size: 13px;
                color: #999;
                span {
                    display: block;
                    border-left: 2px solid transparent;
                }

                &.active {
                    background-color: #f9f9fb;
                    span {
                        border-left-color: #e4393c;
                    }
                }
            }
        }
        .books {
            flex: 10;
            max-height: 79vh;
            overflow-y: scroll;
        }
    }
}
</style>
